<script>
	export default {
		data() {
			return {
				ishow: false,
				teshow: false, //特a特b选中效果
				is_lia_show: false,
				zhenyt: 0, //正1特选中效果  1-6
				screenWidth: document.documentElement.clientHeight,
				content: '',
				zhenyit: ['正1特', '正2特', '正3特', '正4特', '正5特', '正6特'],
				limttle: ['特码', '正码', '正码特', '正码1-6', '过关', '连码', '半波', '一肖/尾数', '特码生肖', '合肖', '生肖连', '尾数连', '全不中'],
				indexs: 0, //俩面盘点击效果
				lianmtitle: ['二全中', '二中特', '特串', '三全中', '三中二', '四全中'],
				hexiao_title: ['二肖', '三肖', '四肖', '五肖', '六肖', '七肖', '八肖', '九肖', '十肖', '十一肖'], //合肖子标题
				hexiao_xz: 0, //合肖选中效果
				shengx_title: ['二肖连中', '三肖连中', '四肖连中', '五肖连中', '二肖连不中', '三肖连部中', '四肖连不中'], //生肖连子标题
				shengx_xz: 0, //生肖连选中效果
				buzhong_title: ['五不中', '六不中', '七不中', '八不中', '九不中', '十不中', '十一不中', '十二不中'], //全不中标题
				buzhong_xz: 0, //生肖连选中效果
				lianmsxs: 0, //连码显示效果,
				yixs: false //一肖尾数切换效果
			}
		},
		props: {
			endTime: {
				type: String,
				default: '1515400000'
			},
			endText: {
				type: String,
				default: '00 : 00 : 00'
			}
		},
		methods: {
			lianms(index) {
				this.lianmsxs = index
			},
			zhentxz(index) {
				console.log(index)
				this.zhenyt = index;
			},
			hexiaos(index) {
				this.hexiao_xz = index;
			},
			shengxiaol(index) {
				this.shengx_xz = index;
			},
			buzhong(index){
				this.buzhong_xz = index;
			},
			kuaij() {
				$('.zongjis #j-all').eq(this.indexs).find('.j-odds').hide();
			},
			yiban() {
				$('.zongjis #j-all').eq(this.indexs).find('.j-odds').show();
			},
			lianm(index) {
				this.indexs = index
				console.log(this.indexs)
			},
			countdowm(timestamp) {
				let self = this;
				let timer = setInterval(function() {
					let nowTime = new Date();
					let endTime = new Date(timestamp * 1000);
					let t = endTime.getTime() - nowTime.getTime();
					if(t > 0) {
						let day = Math.floor(t / 86400000);
						let hour = Math.floor((t / 3600000) % 24);
						let min = Math.floor((t / 60000) % 60);
						let sec = Math.floor((t / 1000) % 60);
						hour = hour < 10 ? "0" + hour : hour;
						min = min < 10 ? "0" + min : min;
						sec = sec < 10 ? "0" + sec : sec;
						let format = '';
						if(day > 0) {
							format = `${day} : ${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour > 0) {
							format = `${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour <= 0) {
							format = `${min} : ${sec}`;
						}
						self.content = format;
					} else {
						clearInterval(timer);
						self.content = self.endText;
					}
				}, 1000);
			}
		},
		mounted: function() {
			let that = this;
			window.onresize = function() {
				that.screenWidth = document.documentElement.clientHeight;
			}
			this.countdowm(this.endTime);

			$('.sxddp label').click(function() {
				var index = $(this).index();
				$('.sxddpx tr').hide();
				$('.sxddpx tr').find('tr').show();
				$('.sxddpx tr').eq(index).show();
			})

		}

	}
</script>
